.counter {
  display: flex;
  justify-content: center;
  align-items: center;
  text-align: center;
  margin: 10px 30px 10px 0;
}

.counter__value {
  height: 43px;
  width: 80px;
  padding: 0 0 0 8px;
  border-radius: 10px;
}

.counter__inc,
.counter__dec {
  display: inline-block;
  background: #ff9f00;
  color: #fff;
  font-weight: bold;
  padding: 10px 18px;
  cursor: pointer;
  border-radius: 10px;
}

.counter__dec {
  margin: 0 10px;
}
.counter__dec:hover,
.counter__inc:hover {
  background: #ff8800;
}

@media screen and (max-width: 767px) {
  .counter {
    margin-bottom: 1.5rem;
  }
}
