@import url(https://fonts.googleapis.com/css2?family=Source+Sans+Pro:ital,wght@0,400;0,700;0,900;1,400&display=swap);
.pd-link {
  text-decoration: none;
  color: inherit;
  transition: 0.3s ease-in-out;
  border-radius: 10px;
}

.pd-link:focus,
.pd-link:hover,
.pd-link:visited,
.pd-link:link,
.pd-link:active {
  text-decoration: none;
  color: inherit;
}

.pd-link:hover {
  transform: translateY(-3px);
}

@media screen and (max-width: 767px) {
  .product-img {
    margin-bottom: 1rem;
  }
}

.counter {
  display: flex;
  justify-content: center;
  align-items: center;
  text-align: center;
  margin: 10px 30px 10px 0;
}

.counter__value {
  height: 43px;
  width: 80px;
  padding: 0 0 0 8px;
  border-radius: 10px;
}

.counter__inc,
.counter__dec {
  display: inline-block;
  background: #ff9f00;
  color: #fff;
  font-weight: bold;
  padding: 10px 18px;
  cursor: pointer;
  border-radius: 10px;
}

.counter__dec {
  margin: 0 10px;
}
.counter__dec:hover,
.counter__inc:hover {
  background: #ff8800;
}

@media screen and (max-width: 767px) {
  .counter {
    margin-bottom: 1.5rem;
  }
}

.btn-container {
  display: flex;
}

.btn-container span:first-child {
  margin-right: 1rem;
}

.btn--primary {
  display: inline-flex;
  justify-content: center;
  align-items: center;
  background: #fb641c;
  padding: 18px 50px;
  color: #fff;
  text-transform: uppercase;
  cursor: pointer;
  border-radius: 2rem;
}
.btn--primary:hover {
  background-color: #fa4f00;
}

.btn--secondary {
  display: inline-flex;
  justify-content: center;
  align-items: center;
  background: rgba(0, 0, 0, 0.09);
  width: 4rem;
  height: 4rem;
  cursor: pointer;
  border-radius: 2rem;
}
.btn--secondary:hover {
  background-color: #fa4f00;
  color: white;
}

@media screen and (max-width: 767px) {
  .btn-container {
    display: flex;
    justify-content: center;
    text-align: center;
  }
}

.wl-link{
    text-decoration: none;
    color: inherit;
    transition: .3s ease-in-out;
}

.wl-link:focus, .wl-link:hover, .wl-link:visited, .wl-link:link, .wl-link:active {
    text-decoration: none;
    color: inherit;
}

.wl-link:hover{
    transform: translateY(-3px);
}
.cart-list .list-group-item + .list-group-item{
  border: 1px solid rgba(0, 0, 0, 0.125) !important;
}

.cart-remove{
    color: #fb641c;
    cursor: pointer;
}

@media screen and (max-width: 767px){
    .cart-img{
      margin-bottom: 1rem;
    }
  }
@media screen and (max-width: 767px){
    .checkout-img{
      width: 100%;
    }
  } 
*::selection{
  background: none;
}

body{
  font-family: 'Source Sans Pro';
}

a:hover{
  text-decoration: none;
  color: #fff;
}
